import './costIcon.scss';
import messages from 'locales/messages';
import React from 'react';
import { injectIntl } from 'react-intl';
const icon = require('./Cost-icon.svg');
const CostIcon = ({ className, intl }) => {
    return (React.createElement("img", { className: `cost-icon ${className}`, src: icon, alt: intl.formatMessage(messages.costManagement), "aria-hidden": "true" }));
};
export default injectIntl(CostIcon);
